cc.Class({
    extends: cc.Component,

    properties: {
        nameLabel_1:cc.Label,

        textLabel_1:cc.Label,

        nameLabel_2:cc.Label,

        textLabel_2:cc.Label,

        questionLabel:cc.Label,

        but_economy:cc.Button,

        gameNode:cc.Node,

        wholeNode:cc.Node,

        springBoard: cc.Node,

        yearsNode:cc.Node,
    },

    onLoad(){


        cc.systemEvent.on('keydown',this.onKeyDown,this);

    },

    onDestroy(){
        cc.systemEvent.off('keydown',this.onKeyDown,this);
    },

    onKeyDown(e){
        switch(e.keyCode){
            case cc.macro.KEY.space:{
                this.allrun();
                break;
            }
        }
    },


    init(textDataArr){
        //
        this.nowText_1 = null;  this.nowText_2=null;    this.nowQuestion=null;
        //
        this.textEnd_1= true;   this.textEnd_2=true;    this.questionEnd=true;
        //
        this.tt = 0;

        this.textIndex = -1;
        this.textDataArr = textDataArr;
        this.node.active = true;
        this.but_economy.node.active = false;

        this.allrun();

    },

    allrun(){
        this.nextQuestionData();
        this.nextTextData_1();
        this.nextTextData_2();
    },
    //
    //
    nextQuestionData(){//

        if(!this.questionEnd)return;
        if(++this.textIndex<this.textDataArr.length){
            this.roleData=this.textDataArr[this.textIndex];
            this.setQuestionData(this.roleData);
        }else{
            this.closeDialog();
        }
    },

    nextTextData_1(){//
        if(!this.textEnd_1)return;
        if(this.textIndex<this.textDataArr.length){
            this.setTextData_1(this.roleData);
        }else{
            this.closeDialog();
        }
    },

    nextTextData_2(){//
        if(!this.textEnd_2)return;

        if(this.textIndex<this.textDataArr.length){
            this.setTextData_2(this.roleData);
        }else{
            this.closeDialog();
        }
    },

    //
    //
    setQuestionData(textData){
        if(!this.questionEnd)return;
        this.questionEnd=false;

        this.questionLabel.string='';
        this.nowQuestion=roleMap[textData.role].question;

    },
    setTextData_1(textData){
        if(!this.textEnd_1)return;
        this.textEnd_1 = false;

        this.nameLabel_1.string= roleMap[textData.role].name;

        this.textLabel_1.string= '';
        this.nowText_1=roleMap[textData.role].choice_one;
        console.log(this.nowText_1);
    },
    setTextData_2(textData){
        if(!this.textEnd_2)return;
        this.textEnd_2 = false;

        this.nameLabel_2.string= roleMap[textData.role].name;
        this.textLabel_2.string= '';
        this.nowText_2=roleMap[textData.role].choice_two;
        console.log(this.nowText_2+'!');
    },
    //
    closeDialog(){
        this.node.active=false;
        this.but_economy.node.active=true;
    },

    //
    update(dt){


        if(!this.nowText_1&&!this.nowText_2&&!this.nowQuestion)return;
        this.tt += dt;
        //tt

        if(this.tt >= 0.1){
            //

            if(this.questionLabel.string.length < this.nowQuestion.length){
                this.questionLabel.string=this.nowQuestion.slice(0,this.questionLabel.string.length+1);

            }else{
                this.questionEnd=true;

            }

            //

            if(this.textLabel_1.string.length < this.nowText_1.length){
                this.textLabel_1.string=this.nowText_1.slice(0,this.textLabel_1.string.length+1);

            }else{
                this.textEnd_1=true;

            }

            //
            if(this.textLabel_2.string.length < this.nowText_2.length){
                this.textLabel_2.string=this.nowText_2.slice(0,this.textLabel_2.string.length+1);

            }else{
                this.textEnd_2=true;

            }
            this.tt=0;//
        }

    },

    //
    leftChoice(textData){
        this.score1+=roleMap[textData.role].left_statistic_1;
        this.score2+=roleMap[textData.role].left_statistic_2;
        this.score3+=roleMap[textData.role].left_statistic_3;
        this.score4+=roleMap[textData.role].left_statistic_4;

        this.returnScore();
    },

    rightChoice(textData){
        this.score1+=roleMap[textData.role].right_statistic_1;
        this.score2+=roleMap[textData.role].right_statistic_2;
        this.score3+=roleMap[textData.role].right_statistic_3;
        this.score4+=roleMap[textData.role].right_statistic_4;
        this.returnScore();
    },

    //
    returnScore(){
        this.gameNode.getComponent('game').score1=this.score1;
        this.gameNode.getComponent('game').score2=this.score2;
        this.gameNode.getComponent('game').score3=this.score3;
        this.gameNode.getComponent('game').score4=this.score4;
    },

    init_score(){
        this.score1=this.gameNode.getComponent('game').score1;
        this.score2=this.gameNode.getComponent('game').score2;
        this.score3=this.gameNode.getComponent('game').score3;
        this.score4=this.gameNode.getComponent('game').score4;
    },
    //button
    left_init(){
        this.init_score();
        this.leftChoice(this.roleData);
        this.printScoreHere();
        this.gameNode.getComponent('game').printScore();
        this.wholeNode.active=false;
        this.but_economy.node.active=true;
        this.springBoard.getComponent('board').spring(roleMap[this.roleData.role].left_statistic_1,
            roleMap[this.roleData.role].left_statistic_2,
            roleMap[this.roleData.role].left_statistic_3,
            roleMap[this.roleData.role].left_statistic_4)

        //
        this.yearsNode.getComponent('years').timeFlow();

    },
    right_init(){
        this.init_score();
        this.rightChoice(this.roleData);
        this.printScoreHere();
        this.gameNode.getComponent('game').printScore();

        this.wholeNode.active=false;
        this.but_economy.node.active=true;

        this.springBoard.getComponent('board').spring(roleMap[this.roleData.role].right_statistic_1,
            roleMap[this.roleData.role].right_statistic_2,
            roleMap[this.roleData.role].right_statistic_3,
            roleMap[this.roleData.role].right_statistic_4)

        //
        this.yearsNode.getComponent('years').timeFlow();
    },

    printScoreHere(){
        console.log(this.score1);
        console.log(this.score2);
        console.log(this.score3);
        console.log(this.score4);}
    },

);

