
cc.Class({
    extends: cc.Component,

    properties: {
        statistic_1Label:cc.Label,
        statistic_2Label:cc.Label,
        statistic_3Label:cc.Label,
        statistic_4Label:cc.Label,
        boardNode:cc.Node,
        gameNode:cc.Node,
    },

    onLoad () {

        this.statistic_1Label.string="";
        this.statistic_2Label.string="";
        this.statistic_3Label.string="";
        this.statistic_4Label.string="";
    },

    updateBoard() {
        this.getScore();

        if (this.score1 < 10) {
            this.statistic_1Label.string = "";
        } else if (this.score1 < 20) {
            this.statistic_1Label.string = "";
        } else if (this.score1 < 30) {
            this.statistic_1Label.string = "";
        } else if (this.score1 < 40) {
            this.statistic_1Label.string = "";
        } else if (this.score1 < 50) {
            this.statistic_1Label.string = "";
        } else if (this.score1 < 60) {
            this.statistic_1Label.string = "";
        } else if (this.score1 < 70) {
            this.statistic_1Label.string = "";
        } else if (this.score1 < 80) {
            this.statistic_1Label.string = "";
        }else if(this.score1<90){
            this.statistic_1Label.string="";
        }else{
            this.statistic_1Label.string="";
        }


        if (this.score2 < 10) {
            this.statistic_2Label.string = "";
        } else if (this.score2 < 20) {
            this.statistic_2Label.string = "";
        } else if (this.score2 < 30) {
            this.statistic_2Label.string = "";
        } else if (this.score2 < 40) {
            this.statistic_2Label.string = "";
        } else if (this.score2 < 50) {
            this.statistic_2Label.string = "";
        } else if (this.score2 < 60) {
            this.statistic_2Label.string = "";
        } else if (this.score2 < 70) {
            this.statistic_2Label.string = "";
        } else if (this.score2 < 80) {
            this.statistic_2Label.string = "";
        } else if (this.score2 < 90) {
            this.statistic_2Label.string="";
        }else{
            this.statistic_2Label.string="";
        }



        if (this.score3 < 10) {
            this.statistic_3Label.string = "...";
        } else if (this.score3 < 20) {
            this.statistic_3Label.string = "";
        } else if (this.score3 < 30) {
            this.statistic_3Label.string = "";
        } else if (this.score3 < 40) {
            this.statistic_3Label.string = "";
        } else if (this.score3 < 50) {
            this.statistic_3Label.string = "";
        } else if (this.score3 < 60) {
            this.statistic_3Label.string = "";
        } else if (this.score3 < 70) {
            this.statistic_3Label.string = "";
        } else if (this.score3 < 80) {
            this.statistic_3Label.string = "";
        }else if(this.score3<90){
            this.statistic_3Label.string="";
        }else{
            this.statistic_3Label.string="";
        }



        if (this.score4 < 10) {
            this.statistic_4Label.string = "";
        } else if (this.score4 < 20) {
            this.statistic_4Label.string = "";
        } else if (this.score4 < 30) {
            this.statistic_4Label.string = "";
        } else if (this.score4 < 40) {
            this.statistic_4Label.string = "";
        } else if (this.score4 < 50) {
            this.statistic_4Label.string = "";
        } else if (this.score4 < 60) {
            this.statistic_4Label.string = "";
        } else if (this.score4 < 70) {
            this.statistic_4Label.string = "";
        } else if (this.score4 < 80) {
            this.statistic_4Label.string = "";
        }else if(this.score4 < 90){
            this.statistic_4Label.string="";
        }else{
            this.statistic_4Label.string="";
        }


        this.boardNode.active=true;
    },


    getScore(){
        this.score1=this.gameNode.getComponent('game').score1;
        this.score2=this.gameNode.getComponent('game').score2;
        this.score3=this.gameNode.getComponent('game').score3;
        this.score4=this.gameNode.getComponent('game').score4;
    },

    close(){
        this.boardNode.active=false;
    }


});
