
let roleMap = {
    1:{
        name:'',
    },
    2:{
        name:'',
    },
    3:{
        name:'',
    },

};

cc.Class({
    extends: cc.Component,

    properties: {
        nameLabel:cc.Label,

        textLabel:cc.Label,

        but_1: cc.Node,

        but_2:cc.Node,

        choice_1: cc.Node,

        choice_2:cc.Node,

        choice_3:cc.Node,

        choice_4:cc.Node,

        lenNode:cc.Node,

        rulerNode:cc.Node,
    },

    onLoad(){


        window.dialog = this.node;

        cc.systemEvent.on('keydown',this.onKeyDown,this);
    },

    onDestroy(){
        cc.systemEvent.off('keydown',this.onKeyDown,this);
    },

    init_but(){
        this.init([
            {role:2,content: ''},
            {role:3,content: ''},
            {role:3,content: ''},
            {role:3,content: ''},
            {role:3,content: '(neutrality)  (net-zero emissions)'},
            {role:3,content:''},
            {role:3,content:''},
            {role:3,content:''},
            {role:3,content:''},
            {role:3,content: ''},
            {role:3,content:''},
            {role:3,content:''},
            //11
            {role:3,content:''},
            {role:3,content:''},


            {role:3,content:''},
            {role:3,content:'(GWP)(GTP)'},
            {role:3,content:''},
            {role:3,content:'(GWP)' +
                    ''},
            {role:3,content:'(GTP)' +
                    'GWP'},
            {role:3,content:''},
            {role:3,content:''},
            //20
            {role:3,content:''},


            {role:3,content:''},
            {role:3,content:''},
            {role:3,content:''},
            {role:3,content:''},
            {role:1,content: ''},
            {role:1,content: ''},
            {role:1,content: ''},
            {role:1,content: ''},
            {role:1,content: ''},
            {role:1,content: ''},
            {role:2,content: ''},

        ]);
    },
    onKeyDown(e){
        switch(e.keyCode){
            case cc.macro.KEY.space:{
                this.updateStop();
                break;
            }
        }
    },

    init(textDataArr){
        this.node.active = true;
        this.but_1.active=false;
        //
        this.nowText = null;
        //
        this.textEnd = true;
        //
        this.tt = 0;

        this.textIndex = -1;
        this.textDataArr = textDataArr;

        this.nextTextData();
    },
    //
    nextTextData(){
        if(!this.textEnd)return;
        this.choice_1.active=false;
        this.choice_2.active=false;
        this.choice_3.active=false;
        this.choice_4.active=false;

        if(this.textIndex==10){
            this.choice_1.active=true;
            this.choice_2.active=true;
        }
        if(this.textIndex==19){
            this.choice_3.active=true;
            this.choice_4.active=true;
        }

        if(++this.textIndex<this.textDataArr.length){
            this.setTextData(this.textDataArr[this.textIndex]);
        }else{
            this.closeDialog();
        }
    },

    //
    setTextData(textData){
        if(!this.textEnd)return;
        this.textEnd = false;

        this.nameLabel.string= roleMap[textData.role].name;
        this.textLabel.string= '';
        this.nowText=textData.content;
    },
    //
    closeDialog(){
        this.node.destroy();

        this.lenNode.active=true;
        this.rulerNode.active=true;
        this.but_2.active=true;
    },

    updateStop(){
        if(this.textLabel.string.length < this.nowText.length){
            this.textLabel.string=this.nowText;
        }else{
            this.nowText="";
            this.nextTextData();
        }
    },
    //
    update(dt){
        if(!this.nowText)return;
        this.tt += dt;
        //tt
        if(this.tt >= 0.05){
            if(this.textLabel.string.length < this.nowText.length){
                this.textLabel.string=this.nowText.slice(0,this.textLabel.string.length+1);

            }else{
                this.textEnd=true;

            }
            this.tt=0;
        }
    }
});

